





















































































































































@import "@design";

.auth-page-body {
  &__form {
    background: #fff;
    width: 65%;
    height: auto;
    padding: 15px 30px;
    border-radius: 5px;
  }

  &__form-row {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
  }

  &__login-button {
    display: flex;
    justify-content: center;
  }

  &__quick-start {
    background: $pale-blue;
    background: #e4ecf2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    text-align: center;

    &--title {
      font-size: 24px;
      color: $red-error;
      font-weight: 700;
    }

    &--program {
      font-size: 20px;
      color: #000;
      font-weight: 500;
    }

    &--planning {
      font-weight: 700;
    }
  }
}
